











import Vue from "vue";
import Component from "vue-class-component";

@Component
export default class Logs extends Vue {
  testData = "";

  async mounted() {
    await Vue.axios
      .get("https://api.spacexdata.com/v3/launches")
      .then((response: any) => {
        this.testData = response.data[0];
      });
  }
}
