import QuickActionButton from "@/components/widgets/QuickActionButton.vue";

@Component({
  components: {
    QuickActionButton
  }
})
export default class Dashboard extends Vue {
  options: ApexOptions = {
    chart: {
      id: "vuechart-example",
      type: "bar",
      height: 600,
      width: 600,
      toolbar: {
        show: false
      }
    },
    xaxis: {
      categories: [1991, 1992, 1993, 1994, 1995, 1996, 1997, 1998]
    }
  };
  series = [
    {
      name: "series-1",
      data: [30, 40, 45, 50, 49, 60, 70, 91]
    }
  ];
}
